
    .custom{
    display:flex;
    text-align:center;
    justify-content:center;
    color:green;
    font-size:40px;
    text-decoration:none;
    font-family:Merryweather;
    
    width:100%;
    }
    
    .green {
      text-decoration: none;
      color: hsla(160, 100%, 37%, 1);
      transition: 0.4s;
    }
    
    * {
        border: none;
    }
    
    h1{
    width:100%;
    text-align:center;
    font-family:Times New Roman;
    }
    
    body {
        background: #73a8da;
        font-family: cursive;
        margin: 0;
       
    }
    
  
    
    #container {
        justify-content: center;
        align-items: center;
        display: flex;
        width: 72%;
        margin:auto;
        height: 400px;
       padding:5px 10px;
        display: flex;
        background-color:rgb(64 64 64);
    }
    
    .square {
        border:none;
        cursor:pointer;
        margin:auto 8px;
        width: 100%;
        height:380px;
        background-color: white;
        
        float: left;
        -webkit-transition: background 0.5s;
        -moz-transition: background 0.5s;
        -o-transition: background 0.5s;
        transition:all background 0.5s;
        justify-content: center;
        align-items: center;
        
    }
    
    
    #color-display {
        display: block;
        text-align:center;
        font-size: 1.3em;
        background: inherit;
        font-family:'Times New Roman';
        font-size:50px;
        font-weight: 700;
    }
    
    #lives,
    #correct, #bestScore {
        display:inline;
        color: #e3dc0f;
        text-shadow: 3px 3px black;
    }
    
   
   
  
    
    #stripe0 .title {
        transition: background-image 1s ease;
        padding:10px 4px;
        flex: 0 0 40%;
        margin:auto;
        font-size: 44px;
        font-family: 'Pacifico', cursive;
        outline:4px solid #a7bce7;
        box-shadow: 0 0 0  4px #002c8c;
        background: -webkit-linear-gradient(45deg, white, black, white, black , white); 
        background-size:20%;
         background: linear-gradient(45deg, rgb(101, 27, 219), black, rgb(121, 23, 206)),blue ,blue ;
         -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        justify-content: center;
        text-align: center;
    
    }
    #stripe0 {
        display: flex;
        align-items: center; /* Acesta aliniază copiii pe verticală */
        justify-content: space-between; /* Acesta aliniază copiii la începutul containerului */
        width: 90%;
        background-color: inherit;
        padding: 10px 0;
        margin: 20px auto;
    }
    
    .mode {
        transition: background-color 0.4s ease-in-out;
        display: flex; /* Face ca .mode să fie și el un container flex */
        align-items: center; /* Centrează conținutul pe verticală */
        justify-content: center; /* Centrează conținutul pe orizontală */
        flex-grow: 1;
        color: white;
        border-radius: 8px;
        text-align: center; /* Acesta asigură că textul este centrat pe orizontală */
        border: none;
        cursor: pointer;
        width: 100%;
    }
    
    #stripe0 .selected {
        background-color: rgba(0, 0, 0, 0.7);
        letter-spacing: 3px;
        transition: 0.3s;
        color:rgb(249 168 212);
        font-weight:600;
        text-shadow: 
        -1px -1px 0px black,  
         1px -1px 0px black,
        -1px  1px 0px black,
         1px  1px 0px pink;
    }
    #reset {
        background-color: lightblue; /* Culoarea inițială */
        transition: background-color 0.5s ease; /* Tranziția suavă a culorii */
    }
    
    button {
        color: #6f95ae;
        font-size: 30px;
        background: white;
        height: 50px;
        cursor: pointer;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        border: 2px solid black;
    }
    
    
    
    
    .infoList{
        width:90%;
        margin:auto;
        display:flex;
        justify-content: center;
        align-items:center;
        font-size:44px;

    }
  
    .infoItem{
        text-align: center;
        border-radius: 8px;
        font-size:36px;
        width:33%;
        letter-spacing: 3px;
        margin:auto;
        color:rgb(249 168 212);
        font-weight:600;
        text-shadow: 
        -1px -1px 0px black,  
         1px -1px 0px pink,
        -1px  1px 0px black,
         1px  1px 0px pink;
         background-color: rgb(30 41 59);
    }
  
    #lives{
        color:green;
    }
  
    #correct{
        color:blue;
    }
    #bestScore{
        color:gold;
    }
  
    #over h1{
        font-size:44px;
    }
  
  
    #over {
        height: 100vh;
        width: 100%;
        background-color: rgb(51 65 85);
        display: block; /* Schimbat de la none pentru a exemplifica afișarea */
        align-items: center;
        justify-content: center;
        text-align: center;
        position: absolute;
        margin-top: -20px;
        flex-direction: column; /* Așează elementele copil în coloană */
        gap: 50px; /* Spațiu între elementele copil */
        
    }
    
    #finalScoreF, #finalBestScoreF {
        display: block;
        width: 40%;
        border-radius:10px;
        margin: 15px auto; 
        padding:10px 4px;
        background-color:inherit;
        font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    }
    
    #finalScoreF {
        margin-top: 5%;
        margin-bottom: 50px;
        border: 5px solid white; 
        color:rgb(249 168 212);
        width: 40%;
       
    }
    
    #finalBestScoreF {
        color: rgb(71, 183, 183);
        border: 5px solid white; 
        width: 40%;
    }
    
    
    #playAgain {
        font-size: 44px;
        text-align: center;
        background-color: white;
        border: 2px solid black;
        width:40%;
        height: 70px;
        position: absolute;
        top: 35%;
        left: 30%;
        margin:5% 0;
        border:3px solid black;
        border-radius:10px;
        color:black;
        cursor:pointer;
    }
    
    